@use "variaveis";

@mixin image(){
    &:hover{
        transform: scale(1.02);
        transition: all 0.4s ease-in-out;
        filter:brightness(120%);
    }
}
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: variaveis.$fonte;
}

html, body{
    scroll-behavior: smooth;
}

body{
    background-color: variaveis.$amarelo;
    color: white;
}

.destaque{
    display: inline;
    padding: 5px 20px;
    border-radius: 20px;
    background-color: variaveis.$azul;
    color: white;
}

.accordion-flush{
    width: 100%;
    .accordion-item{
            --bs-accordion-border-color: transparent;
            background-color: transparent;
            border-bottom: 1px solid black ;
        .accordion-header{
            button{
                background-color: transparent;
                font-size: 19px;
                margin: 10px 0;
                &:focus{
                    box-shadow: none;
                }
            }
        }
    }
}

main{
    header{
        min-height: 100px;
        background-color: transparent;
        z-index: 1;
        article{
            section{
                height: 100px;
                div{
                    color: variaveis.$azul;
                }
                .navbar{
                    ul{
                        .nav-item{
                            
                        }
                    }
                }
            }
        }
    }
    main{
        article{
            section{
                color: #333333;
                .texto{
                    background-color: rgba(0, 0, 0, 0.422);
                    padding: 12px;
                    border-radius: 20px;
                    text-indent: 2em;
                    color: white;
                    text-align: justify;
                    bottom: 0;
                    .estrelas{
                        text-indent: 0;
                        text-align: center;
                    }
                }
            }
            .container_img{
                img{
                    &:hover{
                        transition: all 0.4s ease-in-out;
                        filter:brightness(120%);
                    }
                }
                &::after{
                    content: "";
                    position: absolute;
                    border-radius: 30px;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 20%;
                    background: linear-gradient(to bottom, rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0));
                }
            }
        }
        .pag_02{
            background-color: variaveis.$branco;
            color: black;
            .container_carousel{
                .carousel{
                    .carousel-inner{
                        .carousel-item{
                            div{
                                img{
                                    &:hover{
                                        transform: scale(1.1);
                                        transition: all 0.4s ease-in-out;
                                        filter:brightness(120%);
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
        .pag_03, .pag_05{
            background-color: variaveis.$cinza;
            section{
                .foto_accordion{
                    div{
                        img{
                            @include image;
                        }
                    }
                }
            }
            div{
                color: black;
            }
        }
        .pag_04{
            background-color: variaveis.$branco;
            color: black;
            article{
                .container_divs{
                    position: relative;
                    .div1{
                        background-color: variaveis.$cinza_div;
                        position: sticky;
                        top: 5%;
                        div{
                            img{
                                @include image;
                            }
                        }
                    }
                    .div2{
                        background-color: variaveis.$azul;
                        color: white;
                        .destaque{
                            background-color: white;
                            color: black;
                        }
                    }
                }
            }
        }
        .pag_05{
            .accordion-flush{
                .accordion-item{
                    background-color: variaveis.$cinza_div;
                    border-radius: 15px;
                    border: none;
                }
            }
        }
        .pag_06{
            background-color: variaveis.$azul;
            .destaque{
                background-color: white;
                color: black;
            }
            .container_formulario{
                background-color: variaveis.$branco;
                color: black;
            }
            .contato{
                color: variaveis.$cinza;
            }
            .endereco{
                a{
                    text-decoration: none;
                    color:variaveis.$cinza_div;
                    &:hover{
                        color: variaveis.$amarelo;
                    }
                }
            }
        }
    }
}
footer{
    color: #333333;
    a{
        text-decoration: none;
        color: #333333;
        &:hover{
            color: variaveis.$cinza;
        }
    }
}



